/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.azblocks-custom-modal-block-trigger {
	background: #ff6600;
	color: #fff;
	font-size: 1.1em;
	font-weight: 700;
	border: none;
	border-radius: 24px;
	padding: 0.75em 2em;
	cursor: pointer;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
	margin-top: 0.5em;
	max-width: 400px;
}

.azblocks-custom-modal-block-trigger:hover,
.azblocks-custom-modal-block-trigger:focus {
	background: #e65c00;
}

.wp-block-button__link.az-custom-modal-block {
	background-color: #ff6600;
	color: #fff;
	font-weight: 700;
	border-radius: 4px;
	text-align: center;
}

.disabled {
	opacity: 0.6;
	cursor: not-allowed;
}
